import React from "react";
import { connect } from "react-redux";
import { fetchTopNews } from "../actions/index";
import NewsCard from "./NewsCard";
import moment from "moment";
import MainNews from "./MainNews";
import Loader from "./Loader";

class Home extends React.Component {
  componentDidMount() {
    this.props.fetchTopNews();
  }

  render() {
    if (this.props.loading) {
      return (
        <div>
          <Loader />
        </div>
      );
    } else if (this.props.topNews.articles) {
      const author = this.props.topNews.articles.map(
        (element) => element.source.name
      );

      const title = this.props.topNews.articles.map(
        (element) => element.title.split(" - ")[0]
      );

      const time = this.props.topNews.articles.map(
        (element) => element.publishedAt
      );

      const timestamp = time.map((element) => moment(element).fromNow());

      let newsImage = this.props.topNews.articles.map(
        (element) => element.urlToImage
      );

      for (let i = 0; i < newsImage.length; i++) {
        if (newsImage[i] === null) {
          newsImage[i] =
            "https://upload.wikimedia.org/wikipedia/commons/thumb/d/da/Google_News_icon.svg/1200px-Google_News_icon.svg.png";
        } else if (newsImage[i].includes("bjpenn.com")) {
          newsImage[i] =
            "https://cdn.mmaweekly.com/wp-content/uploads/2017/10/UFC-black-logo-on-gradient.jpg";
        }
      }

      const articleURL = this.props.topNews.articles.map(
        (element) => element.url
      );

      return (
        <div className="home-container">
          <div className="box-container">
            <div className="main-news">
              <a
                href={`${articleURL[0]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <MainNews
                  topNews={this.props.topNews}
                  author={author[0]}
                  title={title[0]}
                  time={timestamp[0]}
                  newsImage={newsImage[0]}
                />
              </a>
            </div>

            <div className="news-card">
              <a
                href={`${articleURL[1]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[1]}
                  title={title[1]}
                  time={timestamp[1]}
                  newsImage={newsImage[1]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[2]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[2]}
                  title={title[2]}
                  time={timestamp[2]}
                  newsImage={newsImage[2]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[3]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[3]}
                  title={title[3]}
                  time={timestamp[3]}
                  newsImage={newsImage[3]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[4]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[4]}
                  title={title[4]}
                  time={timestamp[4]}
                  newsImage={newsImage[4]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[5]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[5]}
                  title={title[5]}
                  time={timestamp[5]}
                  newsImage={newsImage[5]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[6]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[6]}
                  title={title[6]}
                  time={timestamp[6]}
                  newsImage={newsImage[6]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[7]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[7]}
                  title={title[7]}
                  time={timestamp[7]}
                  newsImage={newsImage[7]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[8]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[8]}
                  title={title[8]}
                  time={timestamp[8]}
                  newsImage={newsImage[8]}
                />
              </a>
            </div>
            <div className="news-card">
              <a
                href={`${articleURL[9]}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <NewsCard
                  topNews={this.props.topNews}
                  author={author[9]}
                  title={title[9]}
                  time={timestamp[9]}
                  newsImage={newsImage[9]}
                />
              </a>
            </div>
          </div>
        </div>
      );
    }
    return <div></div>;
  }
}

const mapStateToProps = (state) => {
  return {
    topNews: state.topNews.news,
    loading: state.topNews.loading,
  };
};

const mapDispatchToProps = () => {
  return {
    fetchTopNews,
  };
};

export default connect(mapStateToProps, mapDispatchToProps())(Home);
